body {
  background: url(/memoryclickygame/static/media/background.e3e111e4.png) no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper {
  padding-top: 50px;
  overflow: hidden;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.navbar {
  position: fixed;
  z-index: 99;
  background-color: white;
  width: 100%;
  margin-top: -25px;
}

.brand {
  padding: 10px;
  font-family: 'Black Ops One', cursive;
}
.brand a {
  text-decoration: none;
  color: #064189;
}

.scores {
  padding: 10px;
  margin-right: 10px;
  position: fixed;
  right: 0;
  font-family: 'Oxygen', sans-serif;
  color: #064189
}
.instructions {
    position: absolute;
    top: 100px;
    color: white;
    width: 100%;
    height: 100px;
    text-align: center;
    top: 65px;
    padding-top: 10px;
    font-size: 1.3rem;
    font-family: 'Oxygen', sans-serif;
    background: #e53935;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #e35d5b, #e53935);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #e35d5b, #e53935);
    background: linear-gradient(to right, #e35d5b, #e53935); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.cardContainer {
  margin-top: 150px;
}

.card {
  background: #fff;
  border-radius: 2px;
  /* height: 325px; */
  margin: 1rem;
  position: relative;
  /* width: 325px; */
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card:hover {
  cursor: pointer;
  background: red;
  box-shadow: 0 3px 6px red, 0 3px 6px rgb(158, 47, 47);
}
