.cardContainer {
  margin-top: 150px;
}

.card {
  background: #fff;
  border-radius: 2px;
  /* height: 325px; */
  margin: 1rem;
  position: relative;
  /* width: 325px; */
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card:hover {
  cursor: pointer;
  background: red;
  box-shadow: 0 3px 6px red, 0 3px 6px rgb(158, 47, 47);
}
